import { Injectable } from '@angular/core';

@Injectable()
export class GameService {

  constructor() { }

  public initTargets(): number[] {
    const target: number[] = [];
    for (let i = 0; i < 6; i++) {
      target.push(this.getRandomNumberInterval());
    }
    target.push(25);
    return target;
  }

  public getRandomNumberInterval(): number {
    const min = 7;
    const max = 20;
    return Math.round(Math.random() * (max - min) + min);
  }
}
